/* ===================================
   PORTFOLIO - CURRICULUM VITAE
   Stile moderno, responsive e professionale
   =================================== */

/* ===================================
   VARIABILI E COLORI
   =================================== */

:root {
    --primary: #2563eb;       /* Blu principale */
    --primary-light: #3b82f6; /* Blu chiaro */
    --secondary: #0ea5e9;     /* Cyan/Azzurro */
    --accent: #7c3aed;        /* Viola */
    --danger: #ec4899;        /* Rosa */
    --bg-light: #f8fafc;      /* Grigio chiaro */
    --bg-dark: #0f172a;       /* Grigio scuro */
    --text-dark: #1e293b;     /* Testo scuro */
    --text-light: #475569;    /* Testo chiaro */
    --border: #e2e8f0;        /* Bordi */
    --white: #ffffff;
    --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.1);
    --shadow-md: 0 6px 24px rgba(0, 0, 0, 0.12);
    --shadow-lg: 0 12px 40px rgba(0, 0, 0, 0.15);
}

/* ===================================
   BASE STYLES
   =================================== */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 
                 'Helvetica Neue', Arial, sans-serif;
    line-height: 1.6;
    color: var(--text-dark);
    background: linear-gradient(135deg, #f8fafc 0%, #e0e7ff 100%);
    min-height: 100vh;
}

/* ===================================
   HEADER & NAVIGATION
   =================================== */

.header {
    background: linear-gradient(135deg, var(--bg-dark) 0%, #1a2952 100%);
    color: var(--white);
    padding-top: 0;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    width: 100%;
    box-shadow: var(--shadow-md);
}

.navbar {
    max-width: 1200px;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    justify-content: center;
    background: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(10px);
}

.nav-links {
    list-style: none;
    display: flex;
    gap: 2rem;
    flex-wrap: wrap;
    justify-content: center;
}

.nav-links a {
    color: var(--white);
    text-decoration: none;
    font-weight: 600;
    font-size: 1rem;
    padding: 0.5rem 1rem;
    border-radius: 6px;
    transition: all 0.3s ease;
    position: relative;
}

.nav-links a:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 2px;
    background: linear-gradient(90deg, var(--secondary), var(--primary-light));
    transition: width 0.3s ease;
}

.nav-links a:hover {
    background: rgba(255, 255, 255, 0.1);
}

.nav-links a:hover:before {
    width: 100%;
}

/* ===================================
   HERO SECTION
   =================================== */

.hero {
    background: linear-gradient(135deg, var(--bg-dark) 0%, #1a2952 100%);
    color: var(--white);
    padding: 2rem 2rem;
    text-align: center;
    border-bottom: 3px solid var(--secondary);
    margin-top: 80px;
}

.hero-content {
    max-width: 1200px;
    margin: 0 auto;
}

.hero-title {
    font-size: 3.5rem;
    font-weight: 800;
    margin-bottom: 0.5rem;
    background: linear-gradient(135deg, var(--white), var(--secondary));
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    letter-spacing: -1px;
    animation: slideInDown 0.6s ease;
}

.hero-subtitle {
    font-size: 1.5rem;
    color: var(--secondary);
    margin-bottom: 1rem;
    font-weight: 600;
    animation: slideInUp 0.6s ease 0.1s both;
}

.hero-tagline {
    font-size: 1.1rem;
    color: rgba(255, 255, 255, 0.8);
    font-style: italic;
    animation: slideInUp 0.6s ease 0.2s both;
}

@keyframes slideInDown {
    from {
        opacity: 0;
        transform: translateY(-30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes slideInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* ===================================
   MAIN CONTAINER
   =================================== */

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 2rem;
}

/* ===================================
   SECTIONS
   =================================== */

.section {
    background: var(--white);
    border-radius: 12px;
    padding: 3rem;
    margin-bottom: 2rem;
    box-shadow: var(--shadow-sm);
    transition: all 0.3s ease;
    scroll-margin-top: 200px;
}

.section:hover {
    box-shadow: var(--shadow-md);
    transform: translateY(-2px);
}

.section h2 {
    font-size: 2rem;
    color: var(--primary);
    margin-bottom: 2rem;
    padding-bottom: 1rem;
    border-bottom: 3px solid var(--secondary);
    display: inline-block;
    font-weight: 700;
}

/* ===================================
   ABOUT SECTION
   =================================== */

.about-content {
    display: grid;
    grid-template-columns: 200px 1fr;
    gap: 2rem;
    align-items: start;
}

.about-image {
    display: flex;
    justify-content: center;
}

.profile-pic {
    width: 200px;
    height: 200px;
    border-radius: 12px;
    object-fit: cover;
    border: 4px solid var(--primary);
    box-shadow: 0 8px 24px rgba(37, 99, 235, 0.25);
    transition: all 0.3s ease;
}

.profile-pic:hover {
    transform: scale(1.05);
    box-shadow: 0 12px 32px rgba(37, 99, 235, 0.35);
}

.about-text p {
    margin-bottom: 1.5rem;
    color: var(--text-light);
    line-height: 1.8;
    font-size: 1.05rem;
}

.about-text p:first-child {
    font-size: 1.2rem;
    color: var(--primary);
    font-weight: 600;
}

/* ===================================
   EDUCATION SECTION
   =================================== */

.education-list {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.education-card {
    background: linear-gradient(135deg, var(--bg-light) 0%, rgba(14, 165, 233, 0.05) 100%);
    padding: 1.5rem;
    border-left: 5px solid var(--primary);
    border-radius: 8px;
    transition: all 0.3s ease;
    cursor: pointer;
}

.education-card:hover {
    box-shadow: var(--shadow-md);
    transform: translateX(4px);
    background: linear-gradient(135deg, #f0f9ff 0%, rgba(14, 165, 233, 0.1) 100%);
}

.education-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 1rem;
    margin-bottom: 0.5rem;
    flex-wrap: wrap;
}

.education-card h3 {
    font-size: 1.3rem;
    color: var(--primary);
    margin: 0;
    font-weight: 700;
}

.education-year {
    background: var(--primary);
    color: var(--white);
    padding: 0.4rem 1rem;
    border-radius: 20px;
    font-size: 0.9rem;
    font-weight: 600;
    white-space: nowrap;
}

.education-school {
    color: var(--secondary);
    font-weight: 600;
    margin: 0.5rem 0 0;
    font-size: 1rem;
}

.education-description {
    color: var(--text-light);
    line-height: 1.8;
    margin: 1rem 0 0;
}

/* ===================================
   HOBBIES SECTION
   =================================== */

.hobbies-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
}

.hobby-card {
    background: linear-gradient(135deg, var(--bg-light) 0%, rgba(124, 58, 237, 0.05) 100%);
    padding: 1.5rem;
    border-radius: 12px;
    border: 2px solid var(--border);
    transition: all 0.3s ease;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.hobby-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--primary), var(--accent), var(--secondary));
    transform: scaleX(0);
    transform-origin: left;
    transition: transform 0.3s ease;
}

.hobby-card:hover {
    box-shadow: var(--shadow-lg);
    transform: translateY(-8px);
    border-color: var(--accent);
    background: linear-gradient(135deg, #f5f3ff 0%, rgba(124, 58, 237, 0.1) 100%);
}

.hobby-card:hover::before {
    transform: scaleX(1);
}

.hobby-card h3 {
    font-size: 1.3rem;
    color: var(--accent);
    margin-bottom: 1rem;
    font-weight: 700;
}

.hobby-card p {
    color: var(--text-light);
    line-height: 1.8;
    font-size: 1rem;
}

/* ===================================
   CONTACT SECTION
   =================================== */

.contact-info {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
    margin-top: 1.5rem;
}

.contact-item {
    background: linear-gradient(135deg, var(--bg-light) 0%, rgba(14, 165, 233, 0.05) 100%);
    padding: 1.5rem;
    border-radius: 10px;
    border-left: 4px solid var(--primary);
    transition: all 0.3s ease;
}

.contact-item:hover {
    box-shadow: var(--shadow-md);
    transform: translateY(-4px);
    background: linear-gradient(135deg, #f0f9ff 0%, rgba(14, 165, 233, 0.1) 100%);
}

.contact-item strong {
    display: block;
    color: var(--primary);
    font-size: 1rem;
    margin-bottom: 0.5rem;
}

.contact-item a {
    color: var(--secondary);
    text-decoration: none;
    font-weight: 600;
    word-break: break-all;
    transition: all 0.2s ease;
}

.contact-item a:hover {
    color: var(--primary);
    text-decoration: underline;
}

/* ===================================
   FOOTER
   =================================== */

.footer {
    background: var(--bg-dark);
    color: var(--white);
    text-align: center;
    padding: 2rem;
    margin-top: 3rem;
    font-size: 0.95rem;
}

.footer p {
    margin: 0;
}

/* ===================================
   RESPONSIVE DESIGN
   =================================== */

@media (max-width: 768px) {
    .hero-title {
        font-size: 2.5rem;
    }

    .hero-subtitle {
        font-size: 1.2rem;
    }

    .section {
        padding: 1.5rem;
    }

    .section h2 {
        font-size: 1.5rem;
    }

    .about-content {
        grid-template-columns: 1fr;
        text-align: center;
    }

    .profile-pic {
        width: 150px;
        height: 150px;
    }

    .nav-links {
        gap: 0.5rem;
        font-size: 0.9rem;
    }

    .nav-links a {
        padding: 0.4rem 0.8rem;
    }

    .education-header {
        flex-direction: column;
        gap: 0.5rem;
    }

    .education-year {
        display: inline-block;
        align-self: flex-start;
    }

    .hobby-card h3 {
        font-size: 1.1rem;
    }

    .contact-info {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 480px) {
    .container {
        padding: 1rem;
    }

    .hero {
        padding: 2rem 1rem;
    }

    .hero-title {
        font-size: 1.8rem;
    }

    .hero-subtitle {
        font-size: 1rem;
    }

    .section {
        padding: 1rem;
        border-radius: 8px;
    }

    .nav-links {
        flex-direction: column;
        gap: 0;
    }

    .nav-links a {
        width: 100%;
        text-align: center;
    }

    .about-text p {
        font-size: 1rem;
    }

    .hobby-card {
        padding: 1rem;
    }
}

/* ===================================
   UTILITY CLASSES
   =================================== */

.text-center {
    text-align: center;
}

.mt-1 { margin-top: 0.5rem; }
.mt-2 { margin-top: 1rem; }
.mt-3 { margin-top: 1.5rem; }

.mb-1 { margin-bottom: 0.5rem; }
.mb-2 { margin-bottom: 1rem; }
.mb-3 { margin-bottom: 1.5rem; }

/* ===================================
   PRINT STYLES
   =================================== */

@media print {
    body {
        background: var(--white);
        color: var(--text-dark);
    }

    .header {
        box-shadow: none;
        border-bottom: 2px solid var(--border);
    }

    .navbar {
        background: var(--white);
    }

    .nav-links a {
        color: var(--primary);
    }

    .section {
        box-shadow: none;
        border: 1px solid var(--border);
        page-break-inside: avoid;
    }

    .footer {
        background: var(--white);
        color: var(--text-dark);
        border-top: 2px solid var(--border);
    }

    a {
        color: var(--primary);
        text-decoration: none;
    }
}
